* {
  margin: 0;
  padding: 0;
  width: default;
}
.Main-heading {
  font-size: 50px;
}
/* ----------------------  Techathon CSS ------------------------- */

.techathon-logo {
  display: block;
  transition: none;
  border: none;
  padding: 3px;
  width: 100%;
}
.techathon-logo:hover {
  transform: none;
}

.techathon-intro {
  font-size: 1.5rem;
  padding: 3% 3%;
  text-align: left;
}

.techathon-intro span {
  font-weight: 400;
}

.register-btn {
  height: 60px;
  width: 200px;
  margin-top: 1rem;
  font-size: 20px;
  background-color: black;
  color: white;
  border-radius: 20px;
}

.register-btn a {
  text-decoration: none;
  color: white;
}

.register-btn:hover {
  background-color: grey;
  color: black;
}

.make-it-large {
  font-size: 40px;
  font-weight: bold;
}
.make-it-bold {
  font-weight: bold;
  text-decoration: underline;
}

.event-details {
  padding: 0 3%;
}

.event-details h1 {
  font-size: 36px;
}

.event-details h2 {
  font-size: 30px;
}

.event-list {
  font-size: 23px;
  padding-left: 25px;
}

.event-list li {
  padding-top: 10px;
}

.event-rounds {
  padding: 0 3%;
}

.event-rounds h3 {
  font-size: 35px;
  padding-top: 10px;
}
.event-rounds ul {
  padding-left: 25px;
}

.event-rounds li {
  padding-top: 10px;
  font-size: 22px;
}

.description {
  width: 86%;
  margin-top: 1rem;
  padding: 0 3%;
}

.description-list {
  font-size: 22px;
  margin-top: 20px;
  padding-left: 25px;
}

.description-list ol {
  margin-left: 4rem;
}

.description-list li h3 {
  font-weight: bold;
  font-size: 25px;
  margin-top: 15px;
}

.description-list li {
  margin-top: 10px;
}

.timeline-heading {
  margin-top: 30px;
  font-size: 70px;
  font-weight: bold;
  padding-left: 40rem;
}

.timeline {
  margin: 0 auto;
  max-width: 80%;
  padding: 25px;
  display: grid;
  grid-template-columns: 1fr 3px 1fr;
  color: #000;
}

.timeline__component {
  margin: 20px 30px 20px 20px;
}

.timeline__component--bg {
  padding: 1.5em;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

/* LEAVE TILL LAST */
.timeline__component--bottom {
  margin-bottom: 0;
}

.timeline__middle {
  position: relative;
  background: darkgray;
}

.timeline__point {
  position: absolute;
  top: 23px;
  left: 50%;
  transform: translateX(-50%);
  width: 15px;
  height: 15px;
  background: white;
  border: 2px solid grey;

  border-radius: 50%;
}

/* LEAVE TILL LAST */
.timeline__point--bottom {
  top: initial;
  bottom: 0;
}

.timeline__date--right {
  text-align: right;
}

.timeline__title {
  margin: 0;
  font-size: 23px;
  color: #363636;
  font-weight: bold;
  text-align: center;
}

.timeline__paragraph {
  margin-top: 9px;
  line-height: 1.5;
  text-align: left;
  font-size: 20px;
}
.timeline__date {
  font-size: 23px;
  font-weight: bold;
  color: #363636;
}
/*********** Techathon *********/
.contents {
  padding: 5%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  max-width: 100%;
}

.speakers-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-content: space-between;
  margin: 2.5%;
}

.speakers-card {
  margin: 20px;
  border: 1px solid black;
  width: 500px;
  max-height: 440px;
  border-radius: 10px;
  overflow-y: auto;
}

.profile-pic {
  height: 300px;
  width: 300px;
  border-radius: 100%;
  object-fit: contain;
}
.title {
  text-align: justify;
}

.Guestlist-title {
  font-size: 20px;
  text-align: center;
}

/************************Sponsors****************************/
.sponsor-images {
  justify-content: center;
}

.sponsor-images img {
  transition: none;
  margin-bottom: 16px;
}

.sponsor-images img:hover {
  transform: none;
}

/*------------------Prizes Area------------------------------*/
.Prize-list-title {
  padding-left: 30px;
  padding-top: 15px;
}
.prize-area {
  padding-left: 35px;
}
.prizes {
  font-size: 30px;
  padding-top: 10px;
  padding-left: 35px;
}
.prize-area h2 {
  font-size: 25px;
  padding-left: 4px;
}
